
html, body {
  overflow: hidden;
  height: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.impress-step {
  text-align: center;
  cursor: pointer;
}
.impress-step img {
  width: 100px;
  height: 100px;
}
.impress-step.active {
  cursor: auto;
}



